body {
    width: 100%;
    margin: auto;
    background-color: $bg-cl;
    color: $white-cl;
    overflow-x: hidden;
    //@include text($fs-body, $font-main, $lh-body, $black-cl, none);

    .container {
        width: 100%;
        padding-right: 6.4rem;
        padding-left: 6.4rem;
    }

    .--grid {
        display: grid;
        grid-template-columns: repeat(16, 1fr);
        grid-auto-rows: auto;
        column-gap: 3.6rem;
    }

    @include sm {}

