// set default
a {
    color: $white-cl;
    text-decoration: none;
}

// Heading
.heading {
    font-family: i;

    &.--bold {
        font-family: ib;
    }

    &.--h1 {
        font-size: $fs-h1;
        line-height: $lh-h1;
    }

    &.--h2 {
        font-size: 12rem;
        line-height: 1.2em;
    }

    &.--h3 {
        font-size: 7rem;
        line-height: 1.2em;
    }

    &.--h5 {
        font-size: 2rem;
        line-height: 2.8rem;

        &.--medium {
            font-family: pm;
        }
    }
}

// Body text
.txt {
    font-family: il;

    &.--bold {
        font-family: isb;
    }

    &.--t12 {
        font-size: 1.2rem;
        line-height: 2em;
    }

    &.--t14 {
        font-size: 1.4rem;
        line-height: 2.2rem;
    }

    &.--t16 {
        font-size: 1.6rem;
        line-height: 3rem;
    }
}