/*primary colors*/
$bg-cl: #0E0E0E;

$white-cl: #fff;
$white80-cl: rgba(255, 255, 255, 0.8);
$white50-cl: rgba(255, 255, 255, 0.5);
$white40-cl: rgba(255, 255, 255, 0.4);
$white30-cl: rgba(255, 255, 255, 0.3);
$white10-cl: rgba(255, 255, 255, 0.1);
$white05-cl: rgba(197, 112, 112, 0.05);

$black-cl: #121212;
$black90-cl: #2a2a2a;
$black80-cl: #414141;
$black70-cl: #595959;
$black60-cl: #717171;
$black50-cl: #888888;
$black40-cl: #a0a0a0;
$black30-cl: #b8b8b8;
$black20-cl: #d0d0d0;
$black10-cl: #e7e7e7;
$black05-cl: #f3f3f3;

// border-color
$border-cl: #d0d0d0;

/*headings typo*/
$fs-h1: 26rem;
$lh-h1: 23rem;
$fs-h2: 6rem;
$lh-h2: 7.6rem;
$fs-h3: 4.6rem;
$lh-h3: 5.6rem;
$fs-h4: 3.2rem;
$lh-h4: 4.3rem;
$fs-h5: 2.4rem;
$lh-h5: 3.8rem;

$fs-body: 1.6rem;
$lh-body: 2.6rem;

$fs-body-s: 1.4rem;
$lh-body-s: 2.2rem;

$fs-body-xs: 1.2rem;
$lh-body-xs: 2rem;

$fs-body-xxs: 1rem;
$lh-body-xxs: 1.2rem;

// timing transition
$t: 0.5s;
$cubic: cubic-bezier(0.59, 0.23, 0.22, 0.96);

/*font families*/
$font-main: i;

//============================================================//

/*BOXED LAYOUT*/
$container-max-width: 1920px;
$container-width: 1350px; // tùy thuộc vào container của bản thiết kế mà chỉnh sửa cho hợp lý

/*GRID - media queries breakpoints*/
$xxs-min: 576px; // CÓ THỂ TÙY CHỈNH
$xs-min: 768px; // KHÔNG THAY ĐỔI
$sm-min: 992px; // KHÔNG THAY ĐỔI
$md-min: 1280px; // KHÔNG THAY ĐỔI
$lg-min: 1450px; // CÓ THỂ TÙY CHỈNH

$xxs-max: (
    $xxs-min - 1
); // Small Mobile
$xs-max: (
    $xs-min - 1
); // Mobile
$sm-max: (
    $sm-min - 1
); // Tablet
$md-max: (
    $md-min - 1
); // Desktop
$lg-max: (
    $lg-min - 1
); // Large Desktop